.filmItemWrapper {
  position: relative;
}

.button {
  position: absolute;
  border: none;
  z-index: 10;
  width: 50px;
  height: 50px;
  cursor: pointer;
  outline: none;
  padding: 10px;
  box-sizing: border-box;
  background: #cf7a05;
  border-radius: 50px;
  font-size: 20px;
  text-align: center;
  color: #e1e4d9;
  box-shadow: 0px 0px 10px 5px rgb(32, 39, 39);
}


.link {
  height: 50px;
  width: 50px;
  display: block;
  text-decoration: none;
}

.img {
  height: 300px;
}
